import React, { useContext, useState, useEffect } from "react";
import { CalendarContext } from "src/contexts";
import { Reminder } from "src/components";
import { ReactComponent as AddIcon } from "src/assets/add.svg";
import { ReactComponent as ListIcon } from "src/assets/list.svg";
import { Container, RemindersContainer, Icons, Top } from "./styles";

export const Day = ({ day, disabled, current }) => {
  const { openModal, getReminders } = useContext(CalendarContext);

  const [reminders, setReminders] = useState([]);

  useEffect(() => {
    setReminders(getReminders(day));
  }, [getReminders, day]);

  const openNewReminder = () => {
    openModal(day, "new");
  };

  const viewReminders = () => {
    openModal(day, "list");
  };

  return (
    <Container disabled={disabled} current={current}>
      <Top>
        <span>{day}</span>

        {!disabled && (
          <Icons>
            <AddIcon onClick={openNewReminder} />
            {reminders.length > 0 && <ListIcon onClick={viewReminders} />}
          </Icons>
        )}
      </Top>
      <RemindersContainer>
        {
          //  Avoid reminders in disabled days from other months

          !disabled &&
            reminders?.map((reminder) => (
              <Reminder key={reminder.id} reminder={reminder} />
            ))
        }
      </RemindersContainer>
    </Container>
  );
};
