import styled from "styled-components";

export const Text = styled.textarea`
  text-align: center;
  border-radius: 5px;
  padding: 1em;
  border: none;
  resize: none;

  &:focus {
    outline: none;
  }
`;

export const Input = styled.input`
  text-align: center;
  border-radius: 5px;
  padding: 1em;
  border: none;
  height: 50px;
  flex: 1;

  &:focus {
    outline: none;
  }
`;

export const Button = styled.button`
  border-radius: 5px;
  padding: 1em;
  border: none;
  transition: 0.5s;
  background-color: ${({ disabled }) => (disabled ? "grey" : "orange")};
  color: white;
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &:hover {
    background-color: ${({ disabled }) => (disabled ? "grey" : "#fed8b1")};
  }
`;

export const City = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 20px;
`;
