import styled, { keyframes } from "styled-components";

const fadeIn = keyframes`
  from { opacity: 0 }
  to { opacity: 1 }
`;

export const OuterBlur = styled.div`
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  transition: 0.5s;
  display: ${({ isOpen }) => (isOpen ? "flex" : "none")};
  margin: 0;
  animation: ${fadeIn} 0.1s ease 1;
`;

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: ${({ theme }) => theme.background};
  position: fixed;
  top: 25%;
  left: calc(50% - 280px);
  height: 450px;
  width: 560px;
  animation: ${fadeIn} 0.5s ease 1;
  border-radius: 5px;
  box-shadow: 1px 1px 100px rgba(20, 20, 20, 0.9);

  @media all and (max-width: 500px) {
    height: 40%;
    width: 70%;
  }
`;

export const Close = styled.button`
  height: 40px;
  position: absolute;
  top: 1em;
  right: 1em;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: ${({ theme }) => theme.text};

  &:focus {
    outline: none;
  }
`;

export const Header = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;
  margin: 2em;

  h3 {
    font-size: 30px;
    color: ${({ theme }) => theme.text};
  }

  p {
    font-size: 12px;
    color: ${({ theme }) => theme.text};
  }
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  width: 65%;
  height: 280px;
  overflow: auto;
  margin-bottom: 20px;
`;
