import React, { useContext } from "react";
import { CalendarContext } from "src/contexts";
import { Container, Title } from "./styles";

export const Reminder = ({
  selected,
  reminder: { id, title, city, date, time },
  modal
}) => {
  const { openModal } = useContext(CalendarContext);

  const onClick = () => {
    openModal(date, "edit", id);
  };

  return (
    <Container selected={selected} onClick={onClick}>
      <Title modal={modal}>
        <span>{time}</span> | <span>{title}</span>
      </Title>
    </Container>
  );
};
