import React, { useContext, useState, useEffect } from "react";
import { CalendarContext } from "src/contexts";
import { useCalendar } from "src/hooks";
import { Weather } from "src/components";
import { Bottom, Text, Input, Button, City } from "./styles";

export const EditReminder = () => {
  const [id, setId] = useState("");
  const [title, setTitle] = useState("");
  const [city, setCity] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");

  const { closeModal, editReminder, getReminder } = useContext(CalendarContext);

  const { monthEnd, currentDate } = useCalendar();

  useEffect(() => {
    const { id, title, city, date, time } = getReminder();
    setId(id);
    setTitle(title);
    setCity(city);
    setDate(date);
    setTime(time);
  }, [getReminder, currentDate]);

  const updateReminder = () => {
    editReminder({
      id,
      title,
      city,
      date,
      time
    });
    closeModal();
  };

  return (
    <>
      <Text
        placeholder="Title"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        maxLength="30"
      ></Text>
      <City>
        <Input
          placeholder="City"
          value={city}
          onChange={(e) => setCity(e.target.value)}
        ></Input>
        <Weather city={city} date={date} />
      </City>
      <Bottom>
        <Input
          type="number"
          min="1"
          max={monthEnd}
          placeholder="Date"
          value={date}
          // I make sure that the user doesnt enter dates greater than the last of the mont
          onChange={(e) =>
            e.target.value <= monthEnd && setDate(e.target.value)
          }
        ></Input>
        <Input
          type="time"
          placeholder="Time"
          value={time}
          onChange={(e) => setTime(e.target.value)}
        ></Input>
      </Bottom>
      <Button
        disabled={!title || !city || !date || !time}
        onClick={updateReminder}
      >
        Save
      </Button>
    </>
  );
};
