import React, { useContext } from "react";
import { CalendarContext } from "src/contexts";
import { useCalendar } from "src/hooks";
import { NewReminder, RemindersGrid, EditReminder } from "./components";
import { OuterBlur, Container, Close, Header, Content } from "./styles";

export const Modal = () => {
  const { isModalOpen, closeModal, selectedDate } = useContext(CalendarContext);

  const { currentMonth } = useCalendar();

  return (
    <OuterBlur isOpen={!!isModalOpen}>
      <Container>
        <Close onClick={closeModal}>X</Close>
        <Header>
          <h3>
            {isModalOpen === "new"
              ? "New Reminder"
              : isModalOpen === "edit"
              ? "Edit"
              : "Reminders"}
          </h3>
          <p>
            {selectedDate} {currentMonth}
          </p>
        </Header>
        <Content>
          {isModalOpen === "new" ? (
            <NewReminder />
          ) : isModalOpen === "edit" ? (
            <EditReminder />
          ) : (
            <RemindersGrid />
          )}
        </Content>
      </Container>
    </OuterBlur>
  );
};
