import React from "react";
import { useCalendar } from "src/hooks";
import { Week } from "src/components";
import { Container } from "./styles";

export const Board = () => {
  const { weeks } = useCalendar();
  // The board ends rendering the array of weeks
  return (
    <Container>
      {weeks.map(({ key, days }) => (
        <Week key={key} days={days} />
      ))}
    </Container>
  );
};
