import {
  format,
  startOfWeek,
  addDays,
  startOfMonth,
  endOfMonth,
  endOfWeek,
  isSameMonth,
  isSameDay
} from "date-fns";

export const useCalendar = () => {
  const currentDate = new Date();

  const monthYearDateFormat = "MMMM yyyy";
  const currentMonth = format(currentDate, monthYearDateFormat);

  const monthStart = startOfMonth(currentDate);
  const monthEnd = endOfMonth(monthStart);
  const startDate = startOfWeek(monthStart);
  const endDate = endOfWeek(monthEnd);

  const dayDateFormat = "d";
  const weeks = [];

  let day = startDate;
  let key = 0;

  // Iterate over all the days of the month + the remaining days within weeks of the month, giving each day its corresponding value for the assertions of type disabled/selected/etc
  while (day <= endDate) {
    const days = [];
    for (let i = 0; i < 7; i++) {
      const formattedDate = format(day, dayDateFormat);

      const isFromCurrentMonth = isSameMonth(day, monthStart);
      // Push an object with all the data for that day
      days.push({
        date: formattedDate,
        disabled: !isFromCurrentMonth,
        current: isSameDay(day, currentDate) ? true : false
      });
      day = addDays(day, 1);
    }
    // Push an object with all the data for that week (or row of days)
    weeks.push({ days, key });
    key++;
  }

  return {
    currentDate: Number(format(currentDate, "dd")),
    weeks,
    currentMonth,
    monthEnd: Number(format(monthEnd, "dd"))
  };
};
