import React, { useContext, useState } from "react";
import { CalendarContext } from "src/contexts";
import { Weather } from "src/components";
import { Text, Input, Button, City } from "./styles";

export const NewReminder = () => {
  const [title, setTitle] = useState("");
  const [city, setCity] = useState("");
  const [time, setTime] = useState("");

  const { closeModal, addReminder, selectedDate } = useContext(CalendarContext);

  const createReminder = () => {
    // Only create a reminder if the user entered title and city
    !!title && !!city && !!time && addReminder(title, time, city);
    onClose();
  };

  const onClose = () => {
    closeModal();
    setTitle("");
    setCity("");
    setTime("");
  };

  return (
    <>
      <Text
        placeholder="Title"
        value={title}
        onChange={(e) => setTitle(e.target.value)}
        maxLength="30"
      ></Text>
      <City>
        <Input
          placeholder="City"
          value={city}
          onChange={(e) => setCity(e.target.value)}
        ></Input>
        <Weather city={city} date={selectedDate} />
      </City>
      <Input
        type="time"
        placeholder="Time"
        value={time}
        onChange={(e) => setTime(e.target.value)}
      ></Input>
      <Button disabled={!title || !city || !time} onClick={createReminder}>
        Add
      </Button>
    </>
  );
};
