import { createContext, useState, useEffect } from "react";

export const CalendarContext = createContext({});

export const CalendarProvider = ({ children }) => {
  // Reminders
  const [reminders, setReminders] = useState([]);

  const [selectedDate, setSelectedDate] = useState(null);

  const [selectedReminder, setSelectedReminder] = useState(null);

  // Modal
  const [isModalOpen, setIsModalOpen] = useState(null);

  const openModal = (date, modalType, id) => {
    setSelectedDate(date);
    setIsModalOpen(modalType);
    id && setSelectedReminder(id);
  };

  const closeModal = () => setIsModalOpen(null);

  useEffect(() => {
    console.log({ reminders });
  }, [reminders]);

  const addReminder = (title, time, city) => {
    const id = Date.now();
    setReminders((reminders) => [
      ...reminders,
      { id, title, date: selectedDate, time, city }
    ]);
  };

  const getReminders = (date = selectedDate) => {
    const remindersForDate = reminders.filter(
      (reminder) => reminder.date === date
    );
    return remindersForDate;
  };

  const getReminder = () => {
    const reminder = reminders.find(
      (reminder) => reminder.id === selectedReminder
    );
    return reminder;
  };

  const editReminder = (reminderToEdit) => {
    setReminders((reminders) =>
      reminders.map((reminder) =>
        reminder.id === reminderToEdit.id ? reminderToEdit : reminder
      )
    );
  };

  return (
    <CalendarContext.Provider
      value={{
        reminders,
        addReminder,
        editReminder,
        isModalOpen,
        openModal,
        closeModal,
        selectedDate,
        getReminders,
        getReminder,
        selectedReminder
      }}
    >
      {children}
    </CalendarContext.Provider>
  );
};
