import styled from "styled-components";

export const Container = styled.div`
  background-color: ${({ selected }) => (selected ? "white" : "orange")};
  border-radius: 5px;
  transition: 0.5s;
  overflow: hidden;
  width: 100%;
  display: flex;
  cursor: pointer;
  margin: 3px 0;

  &:hover {
    background-color: ${({ selected }) => (selected ? "orange" : "white")};
  }
`;

export const Title = styled.p`
  border-radius: 5px;
  padding: ${({ modal }) => (modal ? "15px" : "4px")};
  font-size: 11px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;

  span:first-of-type {
    color: white;
    border-radius: 5px;
    padding: 2px;
    transition: 0.5s;
  }

  &:hover {
    span:first-of-type {
      color: orange;
    }
  }
`;
