import React, { useContext, useState, useEffect } from "react";
import { CalendarContext } from "src/contexts";
import { Reminder } from "src/components";
import { Container } from "./styles";

export const RemindersGrid = () => {
  const { getReminders } = useContext(CalendarContext);

  const [reminders, setReminders] = useState([]);

  useEffect(() => {
    setReminders(getReminders());
  }, [getReminders]);

  return (
    <Container>
      {reminders?.map((reminder) => (
        <Reminder modal key={reminder.id} reminder={reminder} />
      ))}
    </Container>
  );
};
