import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0.5em 1em;
  height: 110px;
  width: 110px;
  margin: 0.2em;
  background-color: ${({ disabled, selected, current, theme }) =>
    disabled
      ? "rgba(211, 211,211, 0.8)"
      : current
      ? "lightblue"
      : selected
      ? "#fed8b1"
      : theme.body};
  cursor: ${({ disabled }) => (disabled ? "not-allowed" : "pointer")};
  transition: 0.6s;
  border-radius: 5px;

  // pseudo-selectors
  &:hover {
    background-color: ${({ disabled }) => !disabled && "#fed8b1"};

    svg {
      fill: ${({ disabled }) => !disabled && "#363537"};
    }

    span {
      color: ${({ current }) => !current && "#363537"};
    }
  }

  // nested elements
  span {
    font-weight: 500;
    color: ${({ theme, disabled, current }) =>
      current ? "#363537" : disabled ? "#363537" : theme.text};
  }

  svg {
    fill: ${({ theme }) => theme.text};
    margin: 0;
    width: 13px;
    height: 12px;

    &:hover {
      transform: scale(1.2);
      transition: 0.5s;
    }
  }
`;

export const Top = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
`;

export const Icons = styled.div`
  display: flex;
  gap: 5px;
  margin: 0;
`;

export const RemindersContainer = styled.div`
  display: flex;
  flex-flow: column wrap;
  overflow: hidden;
  width: 100%;
`;
