import styled from "styled-components";

export const Container = styled.div`
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  border-radius: 5px;
  width: 100%;
  height: 100%;
`;

export const Icon = styled.img`
  height: 40px;
  width: 40px;
`;
