import { useState, useEffect } from "react";
import axios from "axios";
import { useCalendar } from "src/hooks";

export const useWeather = ({ city, date }) => {
  const [weather, setWeather] = useState("");

  const { currentDate } = useCalendar();

  const getWeather = async ({ city, date, currentDate }) => {
    try {
      const response = await axios(
        `http://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=6317f8466039ff99ba277e28d3913e7a`
      );

      const periodsOf3hsToDate = (Number(date) - currentDate) * 8;

      const weather = response.data.list[periodsOf3hsToDate].weather[0].main;

      setWeather(weather);
    } catch (e) {
      setWeather("");
    }
  };

  useEffect(() => {
    !!city &&
      !!date &&
      !!currentDate &&
      getWeather({ city, date, currentDate });
  }, [city, date, currentDate]);

  return { weather };
};
