import React from "react";
import Loading from "react-simple-loading";
import { useWeather } from "src/hooks";
import rain from "src/assets/rain.png";
import cloud from "src/assets/cloud.png";
import sun from "src/assets/sun.png";
import snow from "src/assets/snowflake.svg";
import { Container, Icon } from "./styles";

export const Weather = ({ city, date }) => {
  const { weather } = useWeather({ city, date });

  const icon =
    weather.toLowerCase() === "rain"
      ? rain
      : weather.toLowerCase() === "clouds"
      ? cloud
      : weather.toLowerCase() === "clear"
      ? sun
      : weather.toLowerCase() === "snow"
      ? snow
      : undefined;

  return (
    <Container>
      {icon ? <Icon src={icon} /> : <Loading size="12px" />}
    </Container>
  );
};
